<template lang="html">
  <div class="fluid" style="padding: 20px">
     <div class="">
          <div class="ui teal dividing header">
              <i class="filter icon"></i>
              <div class="content">
                Echantillon <div class="sub header">  Code: <b><u>{{echantillon.code}}</u></b> </div><i v-if="sending" class="spinner large loading icon" style="position:absolute;top:10px; right:10px;"></i>
              </div>
           </div>   
      <div class="content">
       <!--  <div class="ui grid stackable fluid">
          <div class="five wide column"> 
 -->

          <div class="ui vertically divided grid">
                  <div class="two column row">
                    <div class="six wide column">
                      <h3>Ajout resultat d'analyse</h3>
                          <form class="ui form">
                              <div class="field">
                                  <label>Choisir le type d'analyse</label>
                                  <select class="ui fluid search dropdown" v-model="idPara" >
                                    <option disabled value="">Choisir 1</option>
                                    <option   v-for="item in parametresA" >{{item.type}}</option>
                                  </select>
                                </div>
                              <!-- <div class="field" :class="{error: error.technique}">
                                <label>Technique d'analyse</label>
                                <input type="text" v-model="analyse.technique" placeholder="Nom Technique">
                              </div>
                              <div class="field" :class="{error: error.protocole}">
                                <label>Protocole d'analyse</label>
                                <input type="text" v-model="analyse.protocole" placeholder="Protocole">
                              </div>
                              <div class="field" :class="{error: error.appareillage}">
                                <label>Appareillage</label>
                                <input type="text" v-model="analyse.appareillage" placeholder="Appareillage">
                              </div>
                              <div class="field" :class="{error: error.autre_info}">
                                <label>Autre Info</label>
                                <textarea v-model="analyse.autre_info"  placeholder="Autre Information"></textarea>
                              </div>
                              <div class="field" :class="{error: error.description}">
                                <label>description analyse</label>
                                <textarea v-model="analyse.description"  placeholder="Description"></textarea>
                              </div>
                              <div class="field" v-if="ok">
                                <label>Laboratoire d'analyse</label>
                                <select class="ui fluid search dropdown" name="laboratoire" v-model="analyse.labo"  >
                                  <option   v-for="elt in laboratoire" >{{elt.nom}}</option>
                                </select>
                              </div>
                              <div class="field" v-if="ok">
                                <label>Choisir le technicien</label>
                                <select class="ui fluid search dropdown" name="technicien" v-model="analyse.user">
                                  <option   v-for="user in users" >{{user.name}}</option>
                                </select>
                              </div> -->
                            
                              <button class="ui primary button" @click.prevent="AddAnalyse" type="submit">Terminer</button>
                          </form>
                    </div>
                    <div class="column">
                    <i v-if="sending" class="spinner large loading icon" style="position:absolute;top:10px; right:10px;"></i>
                      <h2>les paramètres de la {{ idPara }}</h2>
                    <table class="ui celled table">
                      <thead>
                      <tr>
                        <th>#</th>
                        <th>Parametre</th>
                        <th>valeur</th>
                        <th>valider</th>
                      </tr>
                      </thead>
                      <tbody>
                          <tr v-for="param in tab_params" class="ui form">
                            <td><div class="ui ribbon label">{{param.id}}</div></td>
                            <td>{{param.nom}}</td>
                            <td>
                              <div class="field" :class="{error: parametre_nom_error}">
                                <input  type="text" name="value"  placeholder="Nom du paramètre">
                              </div>
                            </td>
                            <td><button class="ui primary button" @click.prevent="AddAnalyse(param, value)" type="submit"><i class="plus icon"></i></button></td>
                          </tr>
                      </tbody>
                    </table>
                    </div>
                  </div>
              
                </div>
      </div>

    </div>
  </div>
</template>

<script>
import { typeAnalyseAddUrl, paramsAnalyse,usersUrl,AnalyseUrl, apiDomain,echantillonUrl,laboratoireUrl, getHeader} from '../../../../config'
import toastr from '../../../../assets/toastr.min.js'
  export default {
    data() {
      return {
        value: [],
           ok: false,
           parametresA: [],
            idPara: '',
            tab_params: [], 
             users:[],
           echantillon: {
              id: null,
              description: '',
              idpuit: null,
              code: '',
              pfd_debut: '',
              pfd_fin: '',
              date_prelevement: '',
              couleur: '',
              structure: '',
              autre_info: '',
        },
        detailEchantillon: {
              id: null,
              description: '',
              idpuit: null,
              code: '',
              pfd_debut: '',
              pfd_fin: '',
              date_prelevement: '',
              couleur: '',
              structure: '',
              autre_info: '',
        },
        analyse: {
          id: null,
          type: '',
          appareillage: '',
          protocole: '',
          technique: '',
          description: '',
          labo: '',
          user:''

        },
        laboratoire: {
          id: null,
          nom: '',
          ville: '',
          lng: '',
          lat:''
        },
        error:{
        technique:false,
        protocole:false,
        appareillage:false,
        autre_info:false
      }
   
      };
    },
    computed: {},
    ready() {},
    attached() {},
    methods: {
      getAnalyse(){
           this.$http.get(typeAnalyseAddUrl,{headers: getHeader()}).then(data => {
              toastr.success("Connection au serveur établie")
              this.typeAnalyses =  data.body.analyse
              // console.log(data.body)
              console.log('params')
              this.parametresA = data.body.analyse
              console.log(this.parametresA)
          //     console.log(this.parametresA.analyse[1].type)
          },data => {
            this.sending = false
            toastr.error("Erreur  de chargement des analyses")
          })
      },
      getEchantillon(){
        this.$http.get(echantillonUrl+'/'+this.echantillon.id).then(data => {
                console.log('echantillon chargé!!')
                this.echantillon = data.body

            },data => {
              toastr.error("Impossible d'obtenir l'echantillon Sélectionné")
            })
      },
      getLaboratoire(){
        this.$http.get(laboratoireUrl).then(data => {
                console.log('laboratoires chargés!!')
                console.log(data.body)

                this.laboratoire = data.body

            },data => {
              toastr.error("Impossible d'obtenir les laboratoires")
            })
      },
      getUsers(){
          this.$http.get(usersUrl).then(data => {
                console.log('users chargés!!')
                console.log(data.body)

                this.users= data.body

            },data => {
              toastr.error("Impossible d'obtenir les utilisateurs")
            })
      },
      AddAnalyse(param,value) {

        console.log(param);
        console.log(value);


        //   this.analyse.type = this.idPara
        // console.log(this.analyse)
        // this.sending = true
        //  this.$http.post(AnalyseUrl,this.analyse,{headers: getHeader()}).then(data => {
        //     this.sending = false
        //       // this.$router.push({name: 'puit.show',params:{id: data.body.idsite,idpuit: data.body.id}})

        //   },data => {
        //     console.log(data);
        //     this.sending = false
        //     toastr.error("une erreur serveur c'est produit lors de l'ajout du site")
        //   })
      }
     
    },
      watch: {
          idPara : function (idPara) {
            this.ok =true
            this.$http.get(paramsAnalyse+'/'+idPara).then(data => {
              // console.log('arrivé des paramètre')
                // console.log(data.body)
                toastr.success("chargement en cours...")
                console.log('paramètreAnalyse'+data.body)
                console.log(data.body)
                this.tab_params = data.body
              
            },data => {
              toastr.error("Impossible de charger les paramètres")
            })
          }
        },
    mounted() {
      this.echantillon.id = this.$route.params.idech
      console.log(this.echantillon.id)
      //get param echan
        this.getAnalyse()
        this.getEchantillon()
        // this.getLaboratoire()
        // this.getUsers()
    },
    components: {}
  };
</script>

<style lang="css">
</style>


